import React, { useState } from 'react';
import { Collapse, Button, CardBody, Card } from 'reactstrap';

const Education = (props) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (
    <section className="portfolio-education" id="education">
      <div className="portfolio-narrow-content">
        <div className="row">
          <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box animate__animated animate__zoomInLeft">
            <h2 className="portfolio-heading animate-box">Education</h2>
          </div>
        </div>
        <div className="row">
          <div className="col-md-12 animate-box">
            <Button
              color="secondary"
              onClick={toggle}
              style={{ marginBottom: '1rem' }}
              className="btn-block text-left animate__animated animate__slideInLeft"
            >
              Bachelor's Degree of Science
            </Button>
            <Collapse isOpen={isOpen}>
              <Card>
                <CardBody>
                  <h4>University of Ilorin, Ilorin, Kwara State</h4>
                  <span>BSc. Biochemistry (First Class)</span>
                </CardBody>
              </Card>
            </Collapse>

            <Button
              color="secondary"
              onClick={toggle}
              style={{ marginBottom: '1rem' }}
              className="btn-block text-left animate__animated animate__slideInRight"
            >
              High School Secondary Education
            </Button>
            <Collapse isOpen={isOpen}>
              <Card>
                <CardBody>
                  <h4>El-shaddai Comprehensive High School, Osun State</h4>
                  <span></span>
                </CardBody>
              </Card>
            </Collapse>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Education;
