import React, { Component } from 'react';
import { FaCloudDownloadAlt, FaPhoneAlt } from 'react-icons/fa';


import axios from 'axios'

class Slider extends Component {
  state = {  };

  downloadFile = ()=>{
    axios.get('http://localhost:5000/download').then((res)=>{})
  }
  render() {
    return (
      <>
        <section
          id="portfolio-hero"
          style={{ marginTop: '-20px', marginLeft: '-5px' }}
        >
          <div className="flexslider">
            <li className="slider1">
              <div className="overlay"></div>
              <div className="container-fluid">
                <div className="row">
                  <div className="col-md-6 col-md-offset-3 col-md-pull-3 col-sm-12 col-xs-12 js-fullheight">
                    <div className="slider-text-inner">
                      <div
                        className="desc "
                        style={{ marginTop: '15rem', paddingLeft: '20px' }}
                      >
                        <h1 className='animate__animated animate__zoomInDown '>
                          Hi! <br />
                          I'm Ayomide
                        </h1>
                        <h4>A Software Developer</h4>
                        <p>
                        <a className="btn btn-light btn-learn" href='#contactus'>
                            Contact <FaPhoneAlt />
                          </a>
                          {/* <a href="../assets/file/Popoola dev CV.pdf" className="btn btn-primary btn-learn" target="_blank" download>
                            Download CV <FaCloudDownloadAlt />
                          </a> */}
                          <span onClick={this.downloadFile()} className="btn btn-primary btn-learn" target="_blank" download>
                            Download CV <FaCloudDownloadAlt />
                          </span>
                          
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </li>
          </div>
        </section>
      </>
    );
  }
}

export default Slider;
