import React, { Component } from 'react';
import { GrHtml5, GrCss3, GrReactjs, GrNode } from 'react-icons/gr';
import { FaAngular, FaBootstrap } from 'react-icons/fa';
import { SiRedux } from 'react-icons/si';
import { DiJavascript1 } from 'react-icons/di';

class Service extends Component {
  state = {};
  render() {
    return (
      <>
        <section className="portfolio-services" data-section="services" id="services">
          <div className="portfolio-narrow-content">
            <div className="row">
              <div
                className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box"
                data-animate-effect="fadeInLeft"
              >
                <span className="heading-meta">What I Use?</span>
                <h2 className="portfolio-heading">Technologies That I Use</h2>
              </div>
            </div>
            <div className="row row-pt-md">
              <div className="col-md-4 text-center animate-box">
                <div className="services color-1">
                  <span className="icon">
                    <GrHtml5 size={40} />
                  </span>
                  <div className="desc">
                    <h3>HTML</h3>
                    <p>
                      Web skeleton and web structure which creates a web buildup{' '}
                    </p>
                  </div>
                </div>
              </div>
              <div className="col-md-4 text-center animate-box">
                <div className="services color-2">
                  <span className="icon">
                    <GrCss3 size={30} /> <FaBootstrap size={30} />
                  </span>
                  <div className="desc">
                    <h3>CSS and Bootstrap</h3>
                    <p>
                      Design the pages to improve users experience and its
                      responsiveness
                    </p>
                  </div>
                </div>
              </div>
              <div className="col-md-4 text-center animate-box">
                <div className="services color-3">
                  <span className="icon">
                    <DiJavascript1 size={40} />
                  </span>
                  <div className="desc">
                    <h3>JavaScript</h3>
                    <p>
                      Improves the users interaction with web and also makes the
                      web dynamic
                    </p>
                  </div>
                </div>
              </div>
              <div className="col-md-4 text-center animate-box">
                <div className="services color-4">
                  <span className="icon">
                    <GrReactjs size={30} />
                    <SiRedux size={30} />
                  </span>
                  <div className="desc">
                    <h3>React and Redux</h3>
                    <p>
                      React which is a front-end technology that creates a
                      better interactive user experience at high speed
                    </p>
                  </div>
                </div>
              </div>
              <div className="col-md-4 text-center animate-box">
                <div className="services color-5">
                  <span className="icon">
                    <FaAngular size={40} />
                  </span>
                  <div className="desc">
                    <h3>Angular</h3>
                    <p>
                      Also a front-end technology that builds a web
                      application which operates at maximal speed
                    </p>
                  </div>
                </div>
              </div>
              <div className="col-md-4 text-center animate-box">
                <div className="services color-6">
                  <span className="icon">
                    <GrNode size={45} />
                  </span>
                  <div className="desc">
                    <h3>Express and Node JS </h3>
                    <p>
                      An enviroment that creates the server-side with great
                      performance and creates solutions to web development
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </>
    );
  }
}

export default Service;
