import React, { Component } from 'react';
import { SiBmcsoftware, SiAffinitydesigner } from 'react-icons/si';
import { MdApps } from 'react-icons/md';
import { RiAppsLine } from 'react-icons/ri';
import 'animate.css';


class About extends Component {
  state = {};
  render() {
    return (
      <>
        <section className="portfolio-about"  id="about">
          <div className="portfolio-narrow-content">
            <div className="row">
              <div className="col-md-12">
                <div
                  className="row row-bottom-padded-sm animate-box"
                  data-animate-effect="fadeInLeft"
                >
                  <div className="col-md-12">
                    <div className="about-desc">
                      <h2 className="portfolio-heading">Who Am I?</h2>
                      <p>
                        <strong>Hi I'm Ayomide Popoola,</strong> who creates
                        application that responds to her client's expectation as
                        needed to creating solutions to problems. My passion
                        is to make sure the world and the people in it find easy
                        ways to do complex work of which i have contributed to creating
                        application that solve such problems and also wanting to
                        solve more as needed by the institutions, organizatiions
                        and companies. i have proven my worth in various sectors
                        and i am willing to extend my tentacles to other sectors
                        that prides herself in giving opportunity
                      </p>
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-3 animate-box animate__animated animate__fadeInLeft">
                    <div className="services color-1">
                      <span className="icon2">
                        <SiBmcsoftware />
                      </span>
                      <h3>Intellectual</h3>
                      <p>
                        
                      </p>
                    </div>
                  </div>
                  <div className="col-md-3 animate-box animate__animated animate__fadeInRight">
                    <div className="services color-2">
                      <span className="icon2">
                        <SiAffinitydesigner />
                      </span>
                      <h3>Creativity</h3>
                      <p></p>
                    </div>
                  </div>
                  <div className="col-md-3 animate-box animate__animated animate__fadeInTopLeft">
                    <div className="services color-3">
                      <span className="icon2">
                        <RiAppsLine />
                      </span>
                      <h3>Problem Solving Skills</h3>
                      <p></p>
                    </div>
                  </div>
                  <div className="col-md-3 animate-box animate__animated animate__fadeInBottomRight">
                    <div className="services color-4">
                      <span className="icon2">
                        <MdApps />
                      </span>
                      <h3>Team Working Ability</h3>
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-12 animate__animated animate__fadeInTopLeft">
                    <div className="hire">
                      <h2>
                        I am happy to inform you <br />
                        that i can meet your expectations
                      </h2>
                      <a href="#contactus" className="btn-hire">
                        Hire me
                      </a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </>
    );
  }
}

export default About;
