import React, { Component } from 'react';
import confectionary from '../assets/images/Bread.png';
import testApp from '../assets/images/Quiz_App.png';
import boutikAng from '../assets/images/boutikAng.png';
import inspire from '../assets/images/inspiredRX.png';
import interior from '../assets/images/interior_land.png';
import investment from '../assets/images/investment_land.png';
import learn from '../assets/images/learncodelanding.png';
import logistic from '../assets/images/logistics.png';
import omini from '../assets/images/omini.png';




class Work extends Component {
  state = {
    work: [
      {
        id: 1,
        effect: 'col-md-6 animate-box animate__animated animate__fadeInRight',
        workNum: 'Industrial Company',
        span: 'This is an industrial company site - built with React',
        seen: '100',
        like: '25',
        bgImage: inspire,
        link: '',
      },
      {
        id: 2,
        effect: 'col-md-6 animate-box animate__animated animate__fadeInleft',
        workNum: 'Online Boutique',
        span:
          'This is an App that gives her customer the ability to shop without visiting the mall- built with Angular',
        seen: '100',
        like: '25',
        bgImage:boutikAng,
        link: 'https://confident-lamport-5e36ec.netlify.app/ ',
      },
      {
        id: 3,
        effect: 'col-md-6 animate-box animate__animated animate__fadeInRightBig',
        workNum: 'Recuitment Examination App',
        span: 'This is an online test for employers to test incoming employees and scores are released immediately',
        seen: '50',
        like: '14',
        bgImage: testApp,
        link: 'https://peteray-dev.github.io/Quiz-App/ ',
      },
      {
        id: 4,
        effect: 'col-md-6 animate-box animate__animated animate__fadeInLeftBig',
        workNum: 'Confectionary',
        span:
          'This is inspire by the delivery issues experiences by bakers/chef',
        seen: '40',
        like: '15',
        bgImage: confectionary,
        link: 'https://peteray-dev.github.io/confectionaries/ ',
      },
      {
        id: 5,
        effect: 'col-md-6 animate-box animate__animated animate__fadeInDown',
        workNum: 'Logistics',
        span: 'A interface for logistic company ',
        seen: '48',
        like: '19',
        bgImage: logistic,
        link: 'https://peteray-dev.github.io/logistic-landing/',
      },
      {
        id: 6,
        effect: 'col-md-6 animate-box animate__animated animate__fadeInTop',
        workNum: 'Food Delivery',
        span: 'this interfaces improves the customer interest in ordering for food and its delivery',
        seen: '20',
        like: '5',
        bgImage: omini,
        link: 'https://peteray-dev.github.io/Food-delivery/  ',
      },
      {
        id: 7,
        effect: 'col-md-6 animate-box animate__animated animate__fadeInTopLeft',
        workNum: 'Login landing page',
        span: '',
        seen: '67',
        like: '42',
        bgImage: learn,
        link: 'https://peteray-dev.github.io/login-template/',
      },
      {
        id: 8,
        effect: 'col-md-6 animate-box animate__animated animate__fadeInTopRight',
        workNum: 'Investment front page',
        span: 'investment',
        seen: '82',
        like: '27',
        bgImage: investment,
        link: ' ',
      },
      {
        id: 9,
        effect: 'col-md-6 animate-box animate__animated animate__fadeInBottomLeft',
        workNum: 'interior design company',
        span: '',
        seen: '92',
        like: '41',
        bgImage: interior,
        link: 'https://peteray-dev.github.io/interior/ ',
      },
    ],
  };
  render() {
    return (
      <>
        <section className="portfolio-work" id="work">
          <div className="portfolio-narrow-content">
            <div className="row">
              <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box animate__animated animate__fadeInleft">
                <span className="heading-meta">My Work</span>
                <h2 className="portfolio-heading animate-box">Recent Work</h2>
              </div>
            </div>
            <div className="row row-bottom-padded-sm animate-box animate-box animate__animated animate__fadeInLeft">
              <div className="col-md-12">
                <p className="work-menu">
                  <span>
                    <small className="active">
                      Web App
                    </small>
                  </span>{' '}
                </p>
              </div>
            </div>
            <div className="row">
            {this.state.work.map((item) => {
              return (
                
                  <div className={item.effect} key={item.id}>
                    <div
                      className="project"
                      style={{backgroundImage: `url(${item.bgImage})`}}
                    >
                      <div className="desc">
                        <div className="con">
                          <h3>
                            <a href={item.link} target="_blank">
                              {item.workNum}
                            </a>
                          </h3>
                          <span>{item.span}</span>
                          <a href={item.link} target="_blank" className="btn btn-primary" style={{marginTop: '30px', border: '2px solid'}}>Open</a>
                         
                        </div>
                      </div>
                    </div>
                  </div>
               
              );
            })}
            </div>
        
          </div>
        </section>
      </>
    );
  }
}

export default Work;
