import React, { Component } from 'react';
import { FaPen } from 'react-icons/fa';
import { Link } from 'react-router-dom';

class Experience extends Component {
  state = {
    details: [
      {
        id: 1,
        position: 'Full Stack Developer (MERN/MEAN) at Alabian Solutions',
        year: '2019 - Till Date',
        duty1:
          'Ability to use the front-end and backend to develop high quality software and web applications',
        duty2: 'Develop highly scalable and testable applications, building interfaces that improves users experiences',
        duty3: 'Ability to build, utilize and integrate API/RESTFUL API using the server-side technologies',
        duty4:
          'Relevant ability to use databases and also experienced in application maintenance and deployment',
          duty5:
          'Building a responsive user-interfaces using the front end technologies',
        icon: 'timeline-icon color-1',
      },
      {
        id: 2,
        position: 'Data Analyst',
        year: '2018-2019',
        duty1: 'Usage of python programming language to analyse data',
        duty2: 'Data wrangling, data cleaning and data intuition',
        duty3: 'Data visualization and analysis of various data information',
        duty4:
          'Machine learning Algorithms for predictions – Supervised machine learning, unsupervised machine learning, reinforcement machine learning',
          duty5: 'Excellent Data Management',
          icon: 'timeline-icon color-2',
      },

      {
        id: 3,
        position: 'Computer Operator / Graphics Designer at Glory Press',
        year: '2015-2017',
        duty1: ' Design Programme pages as requested by clients',
        duty2: 'performing printing operations',
        duty3: 'publishing novels, magazines, articles etc',
        duty4: 'Lithography',
        duty5: 'Creative writting and Editing',
        icon: 'timeline-icon color-3',
      },
    ],
  };
  render() {
    return (
      <>
        <section className="portfolio-experience" id="experience">
          <div className="portfolio-narrow-content">
            <div className="row">
              <div
                className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box"
                data-animate-effect="fadeInLeft"
              >
                <span className="heading-meta">Experience</span>
                <h2 className="portfolio-heading animate-box">Work Experience</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="timeline-centered">
                  {this.state.details.map((item) => {
                    return (
                      <article key={item.id} className="timeline-entry animate-box animate__animated animate__fadeInRight" >
                        <div className="timeline-entry-inner">
                          <div className={item.icon}>
                            <FaPen />
                          </div>
                          <div className="timeline-label">
                            <h2>
                              <Link to="#">{item.position}</Link>
                              <span  style={{fontSize:'14px', paddingLeft:'5px'}}>{item.year}</span>
                            </h2>
                            <ul >
                              <li>{item.duty1}</li>
                              <li>{item.duty2}</li>
                              <li>{item.duty3}</li>
                              <li>{item.duty4}</li>
                              <li>{item.duty5}</li>

                            </ul>
                          </div>
                        </div>
                      </article>
                    );
                  })}
                </div>
              </div>
            </div>
          </div>
        </section>
      </>
    );
  }
}

export default Experience;
