import React, { Component } from 'react';
import 'animate.css';

class Skills extends Component {
  render() {
    return (
      <>
        <section className="portfolio-skills" id="skills">
          <div className="_">
            <div className="row">
              <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate_animated animate_fadeInLeft">
                <span className="heading-meta">My Specialty</span>
                <h2 className="portfolio-heading animate-box">My Skills</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12 animate__animated animate__fadeInLeft">
                <p>
                  I am a Javascript fullstack developer who is capable of using
                  its tools to create excellent visuals and applications. These
                  are my skill level, check it out!{' '}
                </p>
              </div>
              <div className="col-md-6 animate-box animate__animated animate__fadeInLeft">
                <div className="progress-wrap">
                  <h3>HTML5</h3>
                  <div className="progress">
                    <div
                      className="progress-bar color-1"
                      role="progressbar"
                      aria-valuenow="90"
                      aria-valuemin="0"
                      aria-valuemax="100"
                      style={{ width: '90%' }}
                    >
                      <span>90%</span>
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-md-6 animate-box animate__animated animate__fadeInRight">
                <div className="progress-wrap">
                  <h3>CSS3 and Bootstrap</h3>
                  <div className="progress">
                    <div
                      className="progress-bar color-2"
                      role="progressbar"
                      aria-valuenow="80"
                      aria-valuemin="0"
                      aria-valuemax="100"
                      style={{ width: '80%' }}
                    >
                      <span>80%</span>
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-md-6 animate-box animate__animated animate__fadeInLeft">
                <div className="progress-wrap">
                  <h3>Javascript</h3>
                  <div className="progress">
                    <div
                      className="progress-bar color-3"
                      role="progressbar"
                      aria-valuenow="80"
                      aria-valuemin="0"
                      aria-valuemax="100"
                      style={{ width: '80%' }}
                    >
                      <span>80%</span>
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-md-6 animate-box animate__animated animate__fadeInRight">
                <div className="progress-wrap">
                  <h3>React / Redux</h3>
                  <div className="progress">
                    <div
                      className="progress-bar color-4"
                      role="progressbar"
                      aria-valuenow="75"
                      aria-valuemin="0"
                      aria-valuemax="100"
                      style={{ width: '75%' }}
                    >
                      <span>75%</span>
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-md-6 animate-box animate__animated animate__fadeInLeft">
                <div className="progress-wrap">
                  <h3>Angular</h3>
                  <div className="progress">
                    <div
                      className="progress-bar color-5"
                      role="progressbar"
                      aria-valuenow="70"
                      aria-valuemin="0"
                      aria-valuemax="100"
                      style={{ width: '70%' }}
                    >
                      <span>70%</span>
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-md-6 animate-box animate__animated animate__fadeInRight">
                <div className="progress-wrap">
                  <h3>Node / Express</h3>
                  <div className="progress">
                    <div
                      className="progress-bar color-6"
                      role="progressbar"
                      aria-valuenow="80"
                      aria-valuemin="0"
                      aria-valuemax="100"
                      style={{ width: '80%' }}
                    >
                      <span>80%</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </>
    );
  }
}

export default Skills;
