import React, { Component } from 'react';
import { RiPhoneLine, RiMessage2Fill } from 'react-icons/ri';
import {SiTwitter, SiFacebook, SiInstagram, SiLinkedin} from 'react-icons/si'
import axios from 'axios';

class Cont extends Component {
  constructor(props) {
    super(props);
    this.state = {
      firstname: '',
      lastname: '',
      email: '',
      subject: '',
      message: '',
      err: null,
    };
  }

  handleChange = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  resetForm(){
    this.setState({firstname: '', lastname:'', email: '',subject:'', message: ''})
}

  render() {
    return (
      <>
        <section className="portfolio-contact" id="contactus">
          <div className="portfolio-narrow-content">
            <div className="row">
              <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box">
                <span className="heading-meta">Get in Touch</span>
                <h2 className="portfolio-heading">Contact</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-5">
                <div className="portfolio-feature portfolio-feature-sm animate-box">
                  <div className="portfolio-icon">
                    <RiMessage2Fill />
                  </div>
                  <div className="portfolio-text">
                    <p>
                      <a  href="mailto: popoolaayomide37@gmail.com ">
                        <span>popoolaayomide37@gmail.com</span>
                      </a>
                    </p>
                  </div>
                </div>

                <div className="portfolio-feature portfolio-feature-sm animate-box">
                  <div className="portfolio-icon">
                    <RiPhoneLine />
                  </div>
                  <div className="portfolio-text">
                    <p>
                      <a href="tel:+234 903 042 5529">+234 903 042 5529</a>
                    </p>
                  </div>
                </div>

                <div className='portfolio-social' style={{listStyleType: 'none', float:'left'}}>
                <span style={{marginLeft: '-100px'}}>Social Media</span>
                <ul>
                  <li >
                    <a href="https://facebook.com/Ayomide340" target="_blank" style={{color: '#3b5998'}}>
                      <SiFacebook />
                    </a>
                  </li>
                  <li>
                    <a href="https://twitter.com/AY_PAP" target="_blank" style={{color: '#00acee'}}>
                      <SiTwitter />
                    </a>
                  </li>
                  <li>
                    <a href="https://instagram.com/pop_ayomide" target="_blank" style={{color: '#fbad50'}}>
                      <SiInstagram />
                    </a>
                  </li>
                  <li>
                    <a href="https://www.linkedin.com/in/ayomide-popoola-60092a12a" target="_blank" style={{color: '#0e76a8'}}>
                      <SiLinkedin />
                    </a>
                  </li>
                </ul>
                </div>
              </div>
              <div className="col-md-7 col-md-push-1">
                <div className="row">
                  <div
                    className="col-md-10 col-md-offset-1 col-md-pull-1 animate-box"
                    data-animate-effect="fadeInRight"
                  >
                    <form method="POST" action='send'
                      onSubmit={(e) => {
                        e.preventDefault();

                        let formData = new FormData();
                        formData.append('firstname', this.state.firstname);
                        formData.append('lastname', this.state.lastname);
                        formData.append('email', this.state.email);
                        formData.append('subject', this.state.subject);
                        formData.append('message', this.state.message);

                        console.log(this.state);

                        axios({
                            method: "POST", 
                            url:"http://localhost:5000/send", 
                            data:  this.state
                          }).then((response)=>{
                            if (response.data.status === 'success'){
                                alert("Message Sent."); 
                                this.resetForm()
                                // msg = `<div className="alert alert-success">
                                // <p>Message sent succesfully <p>
                                // </div>` 
                            }else if(response.data.status === 'fail'){
                                alert("Message failed to send.")
                            }
                          })
                        
                      }}
                      
                      encType = 'application/json'
                    >
                      {/* {msg} */}
                      <div className="form-group">
                        <input
                          type="text"
                          onChange={this.handleChange}
                          value={this.state.firstname}
                          className="form-control"
                          placeholder="FirstName"
                          name="firstname"
                          required
                        />
                      </div>
                      <div className="form-group">
                        <input
                          type="text"
                          onChange={this.handleChange}
                          value={this.state.lastname}
                          className="form-control"
                          placeholder="LastName"
                          name="lastname"
                          required
                        />
                      </div>
                      <div className="form-group">
                        <input
                          type="email"
                          className="form-control"
                          onChange={this.handleChange}
                          value={this.state.email}
                          placeholder="Email"
                          name="email"
                          required
                        />
                      </div>
                      <div className="form-group">
                        <input
                          type="text"
                          className="form-control"
                          onChange={this.handleChange}
                          value={this.state.subject}
                          placeholder="Subject"
                          name="subject"
                          required
                        />
                      </div>
                      <div className="form-group">
                        <textarea
                          name="message"
                          id="message"
                          cols="30"
                          rows="7"
                          className="form-control"
                          placeholder="Message"
                          onChange={this.handleChange}
                          value={this.state.message}
                          required
                        ></textarea>
                      </div>
                      <div className="form-group">
                        <input
                          type="submit"
                          className="btn btn-primary btn-send-message"
                          value="Send Message"
                        /><small>wait for response....</small>
                      </div>
                      
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </>
    );
  }
}

export default Cont;
